export default{
	name:'detailsContent',
	props:{
		toDatas:Object
	},
	data(){
		return{
			videoSrc:'',
			showVedio:false
		}
	},
	watch:{
		toDatas(newValue,oldValue){
			if(newValue.vedio !== ''){
				this.videoSrc = newValue.vedio
				this.showVedio = true
			}else{
				this.showVedio = false
			}
		}
	}
}
